'use strict'

const isValidVersionString = version => /^\d+\.\d+\.\d+$/.test(version)

module.exports = url => {
  const split = url.split('/')
  const artifactId = split[split.length - 3]
  const possibleVersion = split[split.length - 2]
  return isValidVersionString(possibleVersion) ? `${artifactId}-${possibleVersion}` : 'unknown'
}



